<template lang="html">
  <div class="row">
    <div class="col-lg-8 col-md-8 col-sm-12">
      <form-card
        v-for="group of centerGroups"
        :group="group"
        v-model="form"
      ></form-card>
      <div class="card">
        <div class="card-header">
          <h6 class="title">Menu con</h6>
        </div>
        <div class="card-body form-card">
          <div class="row">
            <Tree
              :data="form.children"
              draggable="draggable"
              cross-tree="cross-tree"
              @change="changeTreeMenu"
            >
              <div slot-scope="{data, store}">
                <template v-if="!data.isDragPlaceHolder">
                  <b class="btn-toggle-open" v-if="data.children && data.children.length" @click="store.toggleOpen(data)">{{data.open ? '-' : '+'}} </b>
                  <span>{{data.title}}</span>
                  <div class="pull-right">
                    <i
                      class="btn-edit-menu el-icon-edit"
                      @click="editMenu(data)"
                    ></i>
                    <i
                      class="btn-remove-menu el-icon-delete"
                      @click="removeMenu(data, store)"
                    ></i>
                  </div>
                  <div class="clearfix"></div>
                </template>
              </div>
            </Tree>
            <el-button
              @click="dialogFormCreateVisible = true"
              plain
              class="full-width"
              size="small"
            >
              <i class="el-icon-circle-plus"></i>
              Thm menu con
            </el-button>
          </div>
        </div>
      </div>
    </div>
    <div class="col-lg-4 col-md-4 col-sm-12">
      <form-card
        v-for="group of sideGroups"
        hasTop="true"
        :group="group"
        v-model="form"
      >
      </form-card>
    </div>
    <div class="container-fluid">
      <div class="page-action">
        <el-button @click="cancel">Hy</el-button>
        <el-button type="primary" @click="updateParentMenu">Cp nht</el-button>
      </div>
    </div>
    <el-dialog title="To menu" :visible.sync="dialogFormCreateVisible" width="40%" @keyup.enter.native="storeMenu">
      <el-form :model="formCreateMenu" label-width="120px">
        <el-form-item label="Menu cha">
          <my-select
            :attribute="childMenu"
            placeholder="Menu cha"
            v-model="formCreateMenu.parent_id"
          ></my-select>
        </el-form-item>
        <el-form-item label="Tiu :">
          <el-input
            autocomplete="off"
            v-model="formCreateMenu.title"
            placeholder="Header, footer, sidebar,..."
            :class="errors.has('name')?'border-danger':''"
            v-validate="'required'"
            data-vv-name="name"
            data-vv-as="Tiu "
          ></el-input>
          <span class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</span>
        </el-form-item>
        <el-form-item label="Link">
          <el-col :span="11">
            <my-select
              :attribute="listType"
              placeholder="Chn loi"
              v-model="formCreateMenu.link_type"
            ></my-select>
          </el-col>
          <el-col :span="11" :offset="2">
            <el-input
              v-if="formCreateMenu.link_type == 'custom' || !formCreateMenu.link_type"
              autocomplete="off"
              v-model="formCreateMenu.link"
              placeholder="a ch web..."
            ></el-input>
            <my-select
              v-else-if="formCreateMenu.link_type == 'product'"
              class="full-width"
              placeholder="Chn sn phm"
              :attribute="products"
              v-model="formCreateMenu.link"
            >
            </my-select>
            <my-select
              v-else-if="formCreateMenu.link_type == 'collection'"
              class="full-width"
              placeholder="Chn nhm sn phm"
              :attribute="collections"
              v-model="formCreateMenu.link"
            >
            </my-select>
            <my-select
              v-else-if="formCreateMenu.link_type == 'article'"
              class="full-width"
              placeholder="Chn bi vit"
              :attribute="articles"
              v-model="formCreateMenu.link"
            >
            </my-select>
            <my-select
              v-else-if="formCreateMenu.link_type == 'blog'"
              class="full-width"
              placeholder="Chn nhm bi vit"
              :attribute="blogs"
              v-model="formCreateMenu.link"
            >
            </my-select>
            <my-select
              v-else-if="formCreateMenu.link_type == 'page'"
              class="full-width"
              placeholder="Chn trang ni dung"
              :attribute="pages"
              v-model="formCreateMenu.link"
            >
            </my-select>
            <my-select
              v-else-if="formCreateMenu.link_type == 'gallery'"
              class="full-width"
              placeholder="Chn gallery"
              :attribute="galleries"
              v-model="formCreateMenu.link"
            >
            </my-select>
          </el-col>
        </el-form-item>
        <el-form-item label="Hnh nh">
          <my-image
            v-model="formCreateMenu.image"
          ></my-image>

        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormCreateVisible = false">ng</el-button>
        <el-button type="primary" @click="storeMenu">To menu</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="Sa menu"
      :visible.sync="dialogFormEditVisible"
      width="40%"
      @keyup.enter.native="updateMenu"
      @closed="closeEditMenu"
    >
      <el-form :model="formEditMenu" label-width="120px">
        <el-form-item label="Menu cha">
          <my-select
            :attribute="childMenu"
            placeholder="Menu cha"
            v-model="formEditMenu.parent_id"
            :value="formEditMenu.parent_id"
            :disabled="false"
          ></my-select>
        </el-form-item>
        <el-form-item label="Tiu :">
          <el-input
            autocomplete="off"
            v-model="formEditMenu.title"
            placeholder="Header, footer, sidebar,..."
            :class="errors.has('name')?'border-danger':''"
            v-validate="'required'"
            data-vv-name="name"
            data-vv-as="Tiu "
          ></el-input>
          <span class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</span>
        </el-form-item>
        <el-form-item label="Link">
          <el-col :span="11">
            <my-select
              :attribute="listType"
              placeholder="Chn loi"
              v-model="formEditMenu.link_type"
            ></my-select>
          </el-col>
          <el-col :span="11" :offset="2">
            <el-input
              v-if="formEditMenu.link_type == 'custom'"
              autocomplete="off"
              v-model="formEditMenu.link"
              placeholder="a ch web..."
            ></el-input>
            <my-select
              v-else-if="formEditMenu.link_type == 'product'"
              class="full-width"
              placeholder="Chn sn phm"
              :attribute="products"
              v-model="formEditMenu.link"
            >
            </my-select>
            <my-select
              v-else-if="formEditMenu.link_type == 'collection'"
              class="full-width"
              placeholder="Chn nhm sn phm"
              :attribute="collections"
              v-model="formEditMenu.link"
            >
            </my-select>
            <my-select
              v-else-if="formEditMenu.link_type == 'article'"
              class="full-width"
              placeholder="Chn bi vit"
              :attribute="articles"
              v-model="formEditMenu.link"
            >
            </my-select>
            <my-select
              v-else-if="formEditMenu.link_type == 'blog'"
              class="full-width"
              placeholder="Chn nhm bi vit"
              :attribute="blogs"
              v-model="formEditMenu.link"
            >
            </my-select>
            <my-select
              v-else-if="formEditMenu.link_type == 'page'"
              class="full-width"
              placeholder="Chn trang ni dung"
              :attribute="pages"
              v-model="formEditMenu.link"
            >
            </my-select>
            <my-select
              v-else-if="formEditMenu.link_type == 'gallery'"
              class="full-width"
              placeholder="Chn gallery"
              :attribute="galleries"
              v-model="formEditMenu.link"
            >
            </my-select>
          </el-col>
        </el-form-item>
        <el-form-item label="Hnh nh">
          <my-image
            v-model="formEditMenu.image"
          ></my-image>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormEditVisible = false">ng</el-button>
        <el-button type="primary" @click="updateMenu">Sa menu</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import FormCard from 'src/components/UIComponents/FormCard.vue'
import { Message, MessageBox, Input, Button, Dialog, Form, FormItem, Col, Upload } from 'element-ui';
import MySelect from 'src/components/UIComponents/Select'
import MyImage from 'src/components/UIComponents/Image.vue'
import SelectAjax from 'src/components/UIComponents/SelectAjax'
import {DraggableTree} from 'vue-draggable-nested-tree'
import dataFrom from './menu-form'
import { mapState } from 'vuex'

let _form = {}, _form_edit_menu = {}, firstGroups, secondGroups;

export default {
  components: {
    FormCard,
    ElInput: Input,
    ElButton: Button,
    ElCol: Col,
    ElUpload: Upload,
    ElFormItem: FormItem,
    ElDialog: Dialog,
    ElForm: Form,
    MySelect,
    SelectAjax,
    Message,
    MessageBox,
    MyImage,
    Tree: DraggableTree
  },
  beforeCreate() {
    firstGroups = dataFrom[0].groups;
    secondGroups = dataFrom[1].groups;

    firstGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })
    secondGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })

    _form_edit_menu = {
      parent_id: 0,
      title: '',
      link_type: 'custom',
      link: '',
      image: ''
    }
  },
  data () {
    return {
      centerGroups: firstGroups,
      sideGroups: secondGroups,
      form: _form,
      dialogFormCreateVisible: false,
      dialogFormEditVisible: false,
      formCreateMenu: {
        parent_id: 0
      },
      formEditMenu: _form_edit_menu,
      editParentID: -1,
      editLink: 1,
      childMenu: [],
      imageUrl: '',
      listType: {
        multiple: false,
        options: [
          {
            title: 'a ch web',
            value: 'custom'
          },
          {
            title: 'Nhm sn phm',
            value: 'collection'
          },
          {
            title: 'Sn phm',
            value: 'product'
          },
          {
            title: 'Nhm bi vit',
            value: 'blog'
          },
          {
            title: 'Bi vit',
            value: 'article'
          },
          {
            title: 'Trang ni dung',
            value: 'page'
          },
          {
            title: 'Gallery',
            value: 'gallery'
          },
        ]
      },
      typeSelect: {
        type: 'custom'
      },
      typeSelectEdit: {
        type: 'custom'
      },
      openFirst: true
    }
  },
  computed: {
    ...mapState({
      originalForm: state => state.menuDetail
    }),
    products() {
      let data = this.$store.state.products;
      return this.$util.parseSelect(data);
    },
    collections() {
      let data = this.$store.state.collections;
      return this.$util.parseSelect(data);
    },
    articles() {
      let data = this.$store.state.articles;
      return this.$util.parseSelect(data);
    },
    blogs() {
      let data = this.$store.state.blogs;
      return this.$util.parseSelect(data);
    },
    pages() {
      let data = this.$store.state.pages;
      return this.$util.parseSelect(data);
    },
    galleries() {
      let data = this.$store.state.galleries;
      return this.$util.parseSelect(data);
    },
  },
  mounted() {
    let id = this.$route.params.id;
    this.$store.dispatch('fetchMenuDetail', id);

    this.$store.dispatch('setPageTitle', 'To menu');
    this.$store.dispatch('setCurrentActions', [{
      label: 'Xa',
      type: 'warning',
      icon: '',
      callback: this.remove
    },{
      label: 'Cp nht',
      type: 'primary',
      icon: '',
      callback: this.updateParentMenu
    }])
    this.$store.dispatch('fetchProducts');
    this.$store.dispatch('fetchCollections');
    this.$store.dispatch('fetchArticles');
    this.$store.dispatch('fetchBlogs');
    this.$store.dispatch('fetchPages');
    this.$store.dispatch('fetchGalleries');

    this.formCreateMenu.parent_id = this.originalForm.id;
  },

  methods: {
    cancel() {
      this.$router.push({name: 'AllMenus'});
    },
    remove(){
      let self = this;
      MessageBox.confirm('Bn c chc chn xa khng?', 'Warning', {
        confirmButtonText: 'ng ',
        cancelButtonText: 'Hy b',
        type: 'warning',
        center: true
      }).then(() => {
        self.$store.dispatch('removeMenu', self.form.id).then(res => {
          Message({
            type: 'success',
            message: ' xa menu'
          });
          self.$router.push({name: 'AllMenus'});
        });
      }).catch(() => {
        Message({
          type: 'info',
          message: 'Hy b'
        });
      })
    },
    changeTreeMenu(node, targetTree, oldTree){
      let self = this
      let menus = [];
      let data = targetTree.data;
      let rootID = this.form.id;
      data.forEach(function (element, index) {
        let temp = {}
        temp.id = element.id;
        temp.parent_id = rootID;
        menus.push(temp);
        self.getDataUpdateMenu(element.children, menus);
      })
      this.$store.dispatch('updatePriorityMenu', {menu: menus}).then(result => {
        Message({
          type: 'success',
          message: 'Cp nht thnh cng'
        });
      }).catch(error => {
        Message({
          type: 'warning',
          message: 'C li xy ra'
        });
      })
    },
    getDataUpdateMenu(children, menus){
      let self = this
      if (children.length){
        children.forEach(function (element, index) {
          let temp = {};
          temp.id = element.id;
          temp.parent_id = element.parent.id;
          menus.push(temp);
          self.getDataUpdateMenu(element.children, menus);
        })
      }
    },
    editMenu(element){
      let self = this;
      this.$store.dispatch('fetchMenuDetailEdit', element.id).then(resp => {
        let data = resp;
        self.editParentID = data.parent_id;
        self.editLink = data.link;
        self.formEditMenu = data;
        self.dialogFormEditVisible = true;
      }).catch(function (error) {
        Message({
          type: 'warning',
          message: 'C li xy ra'
        });
      })
    },
    removeMenu(element, store){
      MessageBox.confirm('Bn c chc chn xa khng?', 'Warning', {
        confirmButtonText: 'ng ',
        cancelButtonText: 'Hy b',
        type: 'warning',
        center: true
      }).then(() => {
        this.$store.dispatch('removeMenu', element.id).then(res => {
          Message({
            type: 'success',
            message: ' xa menu'
          });
          store.deleteNode(element);
        });
      }).catch(() => {
        Message({
          type: 'info',
          message: 'Hy b'
        });
      })
    },
    updateParentMenu() {
      let self = this;
      this.$validator.validateAll().then(function (result) {
        if (result) {
          let data = {};
          data.id = self.form.id;
          data.title = self.form.title;
          data.link = self.form.link;
          data.link_type = self.form.link_type;
          data.image = self.form.image;
          data.parent_id = self.form.parent_id;
          self.$store.dispatch('updateMenu', data).then(res => {
            Message({
              message: 'Cp nht thnh cng',
              type: 'success'
            });
          }, res => {
            Message({
              message: res.message,
              type: 'error'
            });
          });
        }
      })
    },
    storeMenu(){
      let self = this;
      self.$store.dispatch('storeMenu', self.formCreateMenu).then(res => {
        Message({
          message: 'To mi thnh cng',
          type: 'success'
        });
        let id = this.$route.params.id;
        this.$store.dispatch('fetchMenuDetail', id);
        self.dialogFormCreateVisible = false;
      }, res => {
        Message({
          message: res.message,
          type: 'error'
        });
      });
    },
    updateMenu() {
      let self = this;
      self.$store.dispatch('updateMenu', self.formEditMenu).then(res => {
        Message({
          message: 'Cp nht thnh cng',
          type: 'success'
        });
        let id = this.$route.params.id;
        this.$store.dispatch('fetchMenuDetail', id);
        self.dialogFormEditVisible = false;
      }, res => {
        Message({
          message: res.message,
          type: 'error'
        });
      });
    },
    closeEditMenu(){
      this.openFirst = true;
    }
  },
  watch: {
    'formCreateMenu.link_type' (newVal, oldVal){
      if (newVal != 'custom') delete this.formCreateMenu.link;
    },
    'formEditMenu.link_type' (newVal, oldVal){
      if (!this.openFirst){
        if (newVal != 'custom') delete this.formEditMenu.link;
      } else{
        this.openFirst = false;
      }
    },
    originalForm(newVal, oldVal){
      this.form = newVal;
      newVal['arr_menu_item'].forEach(function (item) {
        item.value = item.id;
      })
      newVal['arr_menu_item'].unshift({
        title: newVal.title,
        value: newVal.id,
      })
      this.childMenu.multiple = false;
      this.childMenu.options = newVal['arr_menu_item'];
      this.formCreateMenu.parent_id = this.form.id;
    }
  },
  destroyed(){
    this.$store.dispatch('setCurrentActions', []);
    this.$store.state.menuDetail = [];
  }
}
</script>
<style>
  .list-child-menu{
    width: 100%;
  }
  .list-child-menu .item{
    border: 1px solid #ddd;
    width: 100%;
    border-radius: 5px;
    margin-bottom: 5px;
    padding: 5px;
  }
  .list-child-menu .item p{
    margin: 0;
  }
  .list-child-menu .item button, .btn-edit-menu,.btn-remove-menu{
    margin: 0 5px;
  }
  .btn-remove-menu{
    color: red;
  }
  .list-complete-item .item{
    transition: all 1s;
  }
</style>
